import React from 'react';
import Tabs from '../tabs';
import Tab from './Tab';
import getDataAttr from '../_util/getDataAttr';
export class Item extends React.Component {
    render() {
        return <div>{this.props.children}</div>;
    }
}
class AntTabBar extends React.Component {
    constructor() {
        super(...arguments);
        this.getTabs = () => {
            return React.Children.map(this.props.children, (c) => {
                return Object.assign({}, c.props);
            });
        };
        this.renderTabBar = () => {
            const { barTintColor, hidden, prefixCls, tintColor, unselectedTintColor } = this.props;
            const barCls = hidden ? `${prefixCls}-bar-hidden` : `${prefixCls}-bar`;
            const tabsData = this.getTabs();
            const content = tabsData.map((cProps, index) => {
                return <Tab key={index} prefixCls={`${this.props.prefixCls}-tab`} badge={cProps.badge} dot={cProps.dot} selected={cProps.selected} icon={cProps.icon} selectedIcon={cProps.selectedIcon} title={cProps.title} tintColor={tintColor} unselectedTintColor={unselectedTintColor} dataAttrs={getDataAttr(cProps)} onClick={() => cProps.onPress && cProps.onPress()}/>;
            });
            return <div className={barCls} style={{ backgroundColor: barTintColor }}>
      {content}
    </div>;
        };
    }
    render() {
        const { children } = this.props;
        const tabs = this.getTabs();
        let activeIndex = 0;
        tabs.forEach((tab, index) => {
            if (tab.selected) {
                activeIndex = index;
            }
        });
        return (<div className={this.props.prefixCls}>
        <Tabs tabs={tabs} renderTabBar={this.renderTabBar} tabBarPosition="bottom" page={activeIndex < 0 ? undefined : activeIndex} animated={false} swipeable={false}>
          {children}
        </Tabs>
      </div>);
    }
}
AntTabBar.defaultProps = {
    prefixCls: 'am-tab-bar',
    barTintColor: 'white',
    tintColor: '#108ee9',
    hidden: false,
    unselectedTintColor: '#888',
    placeholder: '',
};
AntTabBar.Item = Item;
export default AntTabBar;



